.container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    padding-bottom: 15px;
    width: 75%;
    position: relative;
}

.gif img, .btn-link {
  position: absolute;
}

.btn-link {
  top: 50%;
  left: 5%;
}


.incorrect {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  -webkit-perspective: 1000px;
          perspective: 1000px;
}

.off {
  display: block;
}

.on {
  display: none;
}


.incorrect img{
  display:none;
}

@media only screen and (max-width: 1300px) {
  .gif {
    display: none;
  }
}


